@import "/Users/blehman/workspace/github/proofpoint-infograph/src/styles/config/_variables.scss";
@import "theme/colors";

.data-info {
  position: fixed;
  right: 1rem;
  top: 1rem;
  background: rgba($body-color, 0.4);
  padding: 2rem;
  font-size: 1rem;
  opacity: 0;

  &.shown {
    opacity: 1;
    transition: all 0.6s;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0.25em 0;

      span {
        text-align: right;
        color: $white;
        width: 60%;
      }

      p {
        text-align: left;
        padding-left: 2em;
        strong {
          color: rgba($white, 0.5);
        }
      }
    }
  }

  span, p {
    font-size: 1rem;
    margin: 0;
    min-width: 8em;
    line-height: 1.2em;
  }

}

@media (max-width: $screen-mobile-max) {
  .data-info {
    display: none;
  }
}



