@import "theme/colors";

.flex-container {
  display: flex;
  flex-wrap: wrap;
}

.flex-column {
  flex-direction: column;
}

.child-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: $white;
  padding: 3em 2em;
  max-width: 1200px;
}

.main-container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 55px;

  h1 {
    text-align: center;
    margin-top: 1em;
  }
}

.form-container {
  width: 100%;
  max-width: 620px;
}

.w100 {
  width: 100%;
}

@media (max-width: $screen-mobile-max) {
  .child-container {
    padding: 100px 1em;
  }

  .main-container {
    margin-top: 0;
  }
}



