@import "/Users/blehman/workspace/github/proofpoint-infograph/src/styles/config/_variables.scss";
@import "theme/colors";
@import "utils/mixins";

.datavis {
  position: relative;
  width: 70%;
  overflow: hidden;

  .bg-wrapper {
    transition: all 1s;

    .clouds__container {
      position: fixed;
      right: 0;
      top: 0;
      width: 70vw;
      height: 100vh;
      opacity: 0;
      visibility: hidden;
      transition: all 0.5s;
      transition-delay: none;

      .clouds {
        height: 100%;
        width: 100%;
      }

      .label {
        position: fixed;
        color: $white;
        font-weight: bold;
        text-transform: uppercase;
        text-shadow: 2px 2px 1px rgba($white, 0.4);

        &--domain {
          top: 15vh;
          left: 45vw;
        }

        &--customer {
          right: 5vw;
          top: 50vh;
        }

        &--partner {
          bottom: 15vh;
          left: 40vw;
        }
      }
    }

    .bg-stars {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      transition: all 1s;
    }

    .bg {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 100%;
      fill: rgba($ip-default-fill, 0.8);
      z-index: -1;
      transition: all 0.5s;
      transition-delay: 1s;

      circle {
        r: 3;
        transition: r 0.2s;
      }

      &-front {
        opacity: 0.5;
        transform: rotate(180deg);
      }

      &-mid {
        opacity: 0.4;
        transform: rotate(120deg);
      }

      &-mid-2 {
        opacity: 0.3;
        transform: rotate(210deg);
      }

      &-back {
        opacity: 0.3;
        transform: rotate(90deg);
      }
    }

    &.zoom {
      opacity: 1;
      transition: all 3s;

      .clouds__container {
        visibility: visible;
        opacity: 1;
        transition: all 1s;
        transition-delay: 1s;
      }

      .bg-stars {
        transform: scale(1.3);

        .bg {
          fill: rgba($ip-default-fill, 1);

          &-front {
            opacity: 1;
          }
          
          &-mid,
          &-mid-2 {
            opacity: 0.7;
          }

          &-back {
            opacity: 0.4;
          }
        }
      }
    }
  }


  .fixed-container {
    position: fixed;
    width: 70%;
    height: 100%;
    top: 0;
    right: 0;

    svg {
      position: relative;
      width: 100%;
      height: 100%;

      .ip-circle {
        fill: $ip-default-fill;
        stroke: $ip-default-stroke;
        stroke-width: 1;

        &.friend {
          fill: $ip-friend-fill;
          stroke: $ip-friend-stroke;
          transition: all 0.4s;
        }

        &.foe {
          fill: $ip-foe-fill;
          stroke: $ip-foe-stroke;
          transition: all 0.4s;
        }

        &.foe-target {
          stroke: $ip-foe-fill;
          stroke-width: 3;
        }
      }   

      .domain-placeholder {
        opacity: 0;

        .domain-circles {
          transition: all 0.2s;
        }

        .domain-circle {
          fill: rgba($domain-circle, 0.5);
          stroke: none;
          stroke-width: 0;
          transition: all 0.2s;   
        }

        text {
          font-size: 2.25rem;
          fill: $text-default;
        }

        .r1-circle {
          fill: transparent;
          stroke-width: 2;
          stroke: $r1-color;
          stroke-dasharray: 10, 0.5;
          animation: flow-circle 1s linear infinite;
          transition: all 0.4s;
          opacity: 0;

          &.shown {
            opacity: 1;
          }
        }

        .r2-circle {
          fill: transparent;
          stroke-width: 4;
          stroke: $r2-color;
          stroke-dasharray: 20, 0.5;
          animation: flow-circle 1s linear infinite;
          animation-direction: reverse;
          transition: all 0.4s;
          opacity: 0;

          &.shown {
            opacity: 1;
          }
        }
      }

      line {
        stroke: $line-default;
        fill: none;
        opacity: .5;
        stroke-width: 1;
        stroke-dasharray: 10, 5;
        stroke-linecap: butt;
        animation: flow-line 0.75s linear infinite;

        &.friend {
          stroke: $ip-friend-stroke;
          opacity: 1;
          transition: all 0.4s;
        }

        &.foe {
          stroke: $ip-foe-stroke;
          opacity: 1;
          transition: all 0.4s;
        }
      }

      .intersect-circle {
        fill: $ip-foe-fill;
      }
      .pulse {
        stroke: $ip-foe-stroke; 
        stroke-width: 2px;
        stroke-opacity: 1;
        fill: $ip-foe-fill;
        fill-opacity: 0.5;
        transform-origin: center center;
        animation-duration: 2s;
        animation-name: pulse;
        animation-iteration-count: infinite;
        animation-fill-mode: both;

        &-2 {
          animation-delay: 0.5s;
        }
      }

      defs circle {
        mix-blend-mode: multiply;
      }
    }
  }  
}


@media (max-width: $screen-mobile-max) {
  .datavis {
    width: 100%;
    height: 50vh;

    .bg-wrapper {

      .bg {
        top: 50vh;
      }

      .bg-stars {
        height: 50vh;
        top: auto;
        bottom: 0;
        z-index: 1;
      }

      .clouds__container {
        width: 100vw;
        height: 50vh;
        top: auto;
        bottom: 0;
        z-index: 2;

        .label {

          &--domain {
            top: 60vh;
            left: 30vw;
          }

          &--customer {
            right: 10vw;
            top: 80vh;
          }

          &--partner {
            bottom: 10vh;
            left: 30vw;
          }
        }
      }
    }

    .fixed-container {
      background-color: $black;
      width: 100%;
      height: 50vh;
      top: auto;
      bottom: 0;
    }

  }
}



@keyframes pulse {
  from {
    stroke-width: 2px;
    stroke-opacity: 1;
    fill-opacity: 0.25;
    r: 6;
  }
  to {
    stroke-width: 0;
    stroke-opacity: 0;
    fill-opacity: 0;
    r: 30;
  }
}

@keyframes flow-line {
  from {
    stroke-dashoffset: 15;
  }
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes flow-circle {
  from {
    stroke-dashoffset: 21;
  }
  to {
    stroke-dashoffset: 0;
  }
}



