@import "/Users/blehman/workspace/github/proofpoint-infograph/src/styles/config/_variables.scss";
@import "theme/colors";
@import "utils/mixins";

.home {
  font-size: 3em;
  color: $body-color;
  text-align: center;
}

.wrapper {
	display: flex;
	align-items: stretch;

  .logo {
    position: fixed;
    top: 0;
    left: 0;
    width: 30%;
    padding: 6rem 2rem 2rem 2rem;
    background-color: $white;
    box-shadow: -5px 10px 20px $white;
    z-index: 3;

    &__inner {
      width: 80%;
      margin: 0 auto;
      text-align: left;
    }

    svg {
      width: 80%;
      height: auto;
    }
  }

	.slides {
		width: 30%;
    background-color: $white;
    z-index: 2;
	}

}

@media (max-width: $screen-mobile-max) {
  .home {

  }

  .wrapper {
    flex-direction: column;

    .logo {
      width: 100%;
    }

    .slides {
      width: 100%;
      z-index: 0;
    }
  }
}



