h1 {
  font-family: $font-family-regular;
  font-size: 3.6rem;
  font-weight: bold;
  color: $h1-color;
  padding-bottom: 2rem;
}

h2 {
  font-family: $font-family-regular;
  font-size: 3.2rem;
  color: $h2-color;
}

h3 {
  font-family: $font-family-regular;
  font-size: 2.4rem;
  color: $h3-color;
}

h4 {
  font-family: $font-family-regular;
  font-size: 2rem;
  color: $body-color;
}

h5 {
  font-family: $font-family-regular;
  font-size: 1.6rem;
  color: $body-color;
}

p {
  font-family: $font-family-regular;
  font-size: 2.8rem;
  line-height: 3rem;
  color: #61B3DF; /*$body-color;*/
}

a {
  font-family: $font-family-regular;
  font-size: 1em;
  text-decoration: underline;

  &:hover {
    color: #999;
    cursor: pointer;
  }
}

b, strong {
  font-weight: bold;
}

code {
  padding: 2px 4px;
}

label {
  font-family: $font-family-regular;
  color: #333;
}

button {
  font-family: $font-family-regular;
}



