@import "/Users/blehman/workspace/github/proofpoint-infograph/src/styles/config/_variables.scss";
@import "theme/colors";
@import "utils/mixins";

.slide {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 2rem;
  width: 80%;
  margin: 0 auto;

  &__inner {
    display: flex;
    align-items: stretch;
    width: 100%;
  }

  &__content {
    text-align: left;
    padding-bottom: 20vh;
  }
}

@media (max-width: $screen-mobile-max) {
  .slide {
    height: auto;
    min-height: 60vh;

    &__content {
      padding-bottom: 0;
    }
  }
}



