@import "/Users/blehman/workspace/github/proofpoint-infograph/src/styles/config/_variables.scss";


$invisible: 0.1;
$dim: 0.35;

@mixin block {
  .fixed-container {
    svg {
      @content
    }
  }
}


// FROM YOUR DOMAIN


.from-domain-invisible {
  @include block {
    line[data-domain="your-domain"],
    .intersect-circle-group[data-fromDomain="your-domain"] {
      opacity: $invisible;
    }
  }
}

.from-domain-dim {
  @include block {
    #your-domain .ip-circle {
      opacity: $dim;
    }

    line[data-domain="your-domain"] {
      opacity: $dim;
    }
  }
}


.from-domain-foe-dim {
  @include block {
    #your-domain .ip-circle.foe {
      opacity: $dim;
    }

    line.foe[data-domain="your-domain"] {
      opacity: $dim;
    }
  }
}


.from-domain-friend-dim {
  @include block {
    #your-domain .ip-circle.friend {
      opacity: $dim;
    }

    line.friend[data-domain="your-domain"] {
      opacity: $dim;
    }
  }
}


.from-domain-gray-dim {
  @include block {
    #your-domain .ip-circle {
      &:not(.foe):not(.friend) {
        opacity: $dim;
      }
    }

    line[data-domain="your-domain"] {
      &:not(.foe):not(.friend) {
        opacity: $dim;
      }
    }
  }
}


.from-domain-to-partner-gray-dim {
  @include block {
    #your-domain .ip-circle {
      &:not(.foe):not(.friend) {
        opacity: $dim;
      }
    }

    line[data-domain="your-domain"][data-toDomain="your-partner"] {
      &:not(.foe):not(.friend) {
